.pagination-container {
    display: flex;
    justify-content:  center;
    list-style-type: none;
  
    .pagination-item {
      padding: 0 12px;
      height: 32px;
      text-align: center;
      margin: auto 4px;
      color: black;
      display: flex;
      box-sizing: border-box;
      align-items: center;
      letter-spacing: 0.01071em;
      border-radius: 16px;
      line-height: 1.43;
      font-size: 13px;
  
      &.dots:hover {
        background-color: transparent;
        cursor: pointer;
      }
      &:hover {
        background-color: #FFCCD3;
        color: black;
        cursor: pointer;
      }
  
      &.selected {
        background-color: #E5002A;
        color: white;
      }
  
      &.disabled {
        display: none;
  
        &:hover {
          background-color: transparent;
          cursor: default;
        }
      }
    }
  }

  .pagination-bar {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  