body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Poppins', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(90deg, rgba(255, 234, 239, 1) 0%, rgba(255, 234, 239, 1) 35%, rgba(250, 250, 250, 1) 100%);
}

.other-bg-gradient {
  background: linear-gradient(74.38deg, rgba(255, 255, 255, 0.7) 0%, rgba(255, 255, 255, 0) 100.75%);
}

input[type=checkbox] {
  accent-color: #E5002A;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Track */
::-webkit-scrollbar-track {
  border-radius: 10px;
  cursor: pointer;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #FFEAEF;
  border-radius: 10px;
  cursor: pointer;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #FFEAEF;
}

/* width */
::-webkit-scrollbar {
  width: 8px;
  height: 6px;
}

.arrow::after {
  content: "";
  display: inline-block;
  margin-left: 0.28em;
  vertical-align: 0.09em;
  border-top: 0.42em solid;
  border-right: 0.32em solid transparent;
  border-left: 0.32em solid transparent;
}

.thumbs-wrapper {
  display: none;
}

.carousel-status {
  display: none;

}

.swiper-wrapper {
  margin: 10px 0;
}





.swiper-slide1 {
  /* width: 111% !important; */
  /* width: 100% !important; */
  display: flex !important;
  justify-content: space-between !important;
  align-items: center !important;
  overflow: hidden !important;
  z-index: -10px !important;

}

/* -------------file upload ---------------- */

input[type="file"] {

  display: none;
}

.sidebar {
  transition: all 0.4s ease-in-out;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

/* -------------------swiper------------- */
.swiper-button-next,
.swiper-button-prev {
  background-color: black;
  border-radius: 50%;
  padding: 25px 25px;

}

.swiper-button-next:after,
.swiper-button-prev:after {
  font-size: 15px !important;
  color: white !important;
}



/* select-box */
select {
  appearance: none;
  -webkit-appearance: none; 
   -moz-appearance: none;
}

/* arrows */
select.round {
  background-image: url('https://cdn-icons-png.flaticon.com/512/2985/2985150.png');
  background-position:
    calc(100% - 20px) calc(1em + 2px),
    calc(100% - 15px) calc(1em + 2px),
    calc(100% - .5em) .5em;
  background-size:
    15px 15px,
    5px 5px,
    1.5em 1.5em;
  background-repeat: no-repeat;
}
